/* Main container styles */
.micronutrient-chart-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 1024px;
  margin: 0 auto;
  padding: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

/* Card styles */
.chart-card, .profile-card {
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
  margin-bottom: 1.5rem;
}

/* Chart header */
.chart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.chart-title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #1f2937;
  margin: 0;
}

/* Display mode buttons */
.display-mode-buttons {
  display: flex;
  gap: 0.5rem;
}

.mode-button {
  padding: 0.25rem 0.75rem;
  font-size: 0.875rem;
  border-radius: 0.375rem;
  border: none;
  background-color: #e5e7eb;
  color: #4b5563;
  cursor: pointer;
  transition: all 0.2s ease;
}

.mode-button:hover {
  background-color: #d1d5db;
}

.mode-button-active {
  background-color: #2563eb;
  color: white;
}

.mode-button-deficient {
  background-color: #dc2626;
  color: white;
}

.mode-button-optimal {
  background-color: #16a34a;
  color: white;
}

/* Category filter buttons */
.category-filter {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.category-button {
  padding: 0.25rem 0.75rem;
  font-size: 0.875rem;
  border-radius: 0.375rem;
  border: none;
  background-color: #e5e7eb;
  color: #4b5563;
  cursor: pointer;
  transition: all 0.2s ease;
}

.category-button:hover {
  background-color: #d1d5db;
}

.category-button-active {
  background-color: #2563eb;
  color: white;
}

/* Deficiency alert */
.deficiency-alert {
  background-color: #fffbeb;
  border-left: 4px solid #f59e0b;
  padding: 0.75rem;
  margin-bottom: 1rem;
  color: #92400e;
}

.deficiency-message {
  font-weight: 500;
  margin: 0;
}

/* COVID alert styles */
.covid-alert {
  padding: 0.75rem;
  margin-bottom: 1rem;
  border-left: 4px solid;
}

.covid-title {
  font-weight: 700;
  margin: 0 0 0.25rem 0;
}

.covid-description {
  font-size: 0.875rem;
  margin: 0;
}

.covid-severity-mild {
  background-color: #eff6ff;
  border-color: #3b82f6;
  color: #1e40af;
}

.covid-severity-moderate {
  background-color: #fefce8;
  border-color: #eab308;
  color: #854d0e;
}

.covid-severity-severe {
  background-color: #fef2f2;
  border-color: #ef4444;
  color: #b91c1c;
}

.covid-severity-very-severe {
  background-color: #fff1f2;
  border-color: #e11d48;
  color: #9f1239;
}

/* Chart content styles */
.chart-content {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

/* Bullet chart styles */
.bullet-chart {
  width: 100%;
}

.bullet-chart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.bullet-chart-title {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.nutrient-name {
  font-weight: 600;
  color: #1f2937;
}

.adjusted-badge {
  font-size: 0.625rem;
  padding: 0.125rem 0.375rem;
  background-color: #dbeafe;
  color: #1e40af;
  border-radius: 9999px;
  font-weight: 500;
}

.bullet-chart-values {
  font-size: 0.875rem;
  color: #4b5563;
}

.bullet-chart-percentage {
  margin-left: 0.5rem;
  font-weight: 600;
}

/* The bullet chart track */
.bullet-chart-track {
  position: relative;
  height: 1.5rem;
  background-color: #e5e7eb;
  border-radius: 0.375rem;
  overflow: hidden;
}

/* The actual value bar */
.actual-value-bar {
  position: absolute;
  height: 100%;
  left: 0;
  top: 0;
  border-radius: 0.375rem;
  transition: width 0.5s ease;
}

/* The target line at 100% */
.target-line {
  position: absolute;
  height: 100%;
  width: 2px;
  background-color: #111827;
  top: 0;
}

/* The threshold marker at 70% */
.threshold-marker {
  position: absolute;
  height: 100%;
  width: 2px;
  background-color: #f59e0b;
  top: 0;
  z-index: 1;
}

/* Chart legend */
.chart-legend {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-top: 1.5rem;
  padding-top: 1rem;
  border-top: 1px solid #e5e7eb;
}

.legend-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.legend-color {
  width: 1rem;
  height: 1rem;
  border-radius: 0.25rem;
}

.legend-optimal {
  background-color: #4CAF50;
}

.legend-good {
  background-color: #8BC34A;
}

.legend-moderate {
  background-color: #FFC107;
}

.legend-low {
  background-color: #FF9800;
}

.legend-very-low {
  background-color: #F44336;
}

.legend-target {
  width: 0.125rem;
  height: 1rem;
  background-color: #111827;
}

.legend-threshold {
  width: 0.125rem;
  height: 1rem;
  background-color: #f59e0b;
}

.legend-text {
  font-size: 0.75rem;
  color: #4b5563;
}

/* Profile card styles */
.profile-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #1f2937;
  margin-top: 0;
  margin-bottom: 1rem;
}

.profile-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
}

.profile-item {
  display: flex;
  flex-direction: column;
}

.profile-label {
  font-size: 0.875rem;
  color: #6b7280;
  margin: 0 0 0.25rem 0;
}

.profile-value {
  font-size: 1rem;
  font-weight: 500;
  color: #1f2937;
  margin: 0;
}

.covid-toggle-button {
  font-size: 1rem;
  font-weight: 500;
  color: #1f2937;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  text-align: left;
  text-decoration: underline;
  transition: color 0.2s ease;
}

.covid-toggle-button:hover {
  color: #2563eb;
}

/* Loading indicator */
.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
}

.loading-indicator {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.spinner {
  width: 2.5rem;
  height: 2.5rem;
  border: 0.25rem solid #e5e7eb;
  border-top-color: #2563eb;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.loading-text {
  margin-top: 1rem;
  color: #4b5563;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* Responsive styles */
@media (max-width: 768px) {
  .chart-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .display-mode-buttons {
    margin-top: 0.75rem;
  }
  
  .profile-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .category-filter {
    flex-wrap: wrap;
  }
  
  .bullet-chart-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .bullet-chart-values {
    margin-top: 0.25rem;
  }
}

/* Date navigation styles */
.date-navigation {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
}

.date-nav-button {
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px 15px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.2s;
  white-space: nowrap;
}

.date-nav-button:hover {
  background-color: #e0e0e0;
}

.date-nav-button.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.current-date {
  font-size: 16px;
  font-weight: 500;
  padding: 0 15px;
  text-align: center;
  min-width: 220px;
}

.today-button {
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 8px 15px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.2s;
}

.today-button:hover {
  background-color: #3a80d2;
}

.no-data-message {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  font-size: 16px;
  color: #666;
  font-style: italic;
  background-color: #f9f9f9;
  border-radius: 8px;
  margin: 20px 0;
}

/* Time indicator styles */
.time-indicator {
  font-size: 14px;
  color: #666;
  text-align: right;
  margin-top: 5px;
  margin-bottom: 15px;
}

.timezone-badge {
  display: inline-block;
  background-color: #f0f0f0;
  color: #333;
  padding: 2px 8px;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 500;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .date-navigation {
    flex-direction: column;
    align-items: center;
  }
  
  .current-date {
    margin: 10px 0;
    order: -1;
  }
}