.landing-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  padding: 2rem;
}

.landing-content {
  max-width: 800px;
  text-align: center;
  background: white;
  padding: 3rem;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.landing-content h1 {
  font-size: 3rem;
  color: #2c3e50;
  margin-bottom: 0.5rem;
}

.landing-content h2 {
  font-size: 1.5rem;
  color: #7f8c8d;
  margin-bottom: 2rem;
}

.app-description {
  margin: 2rem 0;
  text-align: left;
  line-height: 1.6;
  color: #34495e;
}

.app-description p {
  margin-bottom: 1rem;
}

.auth-buttons {
  display: flex;
  gap: 18px;
  justify-content: center;
  margin-top: 32px;
}

.landing-auth-button {
  min-width: 140px;
  height: 48px;
  padding: 0 32px;
  background: #1976d2;
  color: #fff;
  border: none;
  border-radius: 6px;
  font-size: 1.1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px rgba(25, 118, 210, 0.08);
  outline: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.landing-auth-button:hover, .landing-auth-button:focus {
  background: #1251a3;
  box-shadow: 0 4px 16px rgba(25, 118, 210, 0.12);
} 