/* ConnectDevicePage.css */
.connect-device-page {
  min-height: calc(100vh - 80px); /* Adjust for header/footer */
  display: flex;
  flex-direction: column;
  background-color: #f9f9fb;
}

.connect-device-page .container {
  max-width: 900px;
  margin: 0 auto;
  padding: 40px 20px;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.connect-device-page h1 {
  font-size: 32px;
  font-weight: 700;
  color: #333;
  margin-bottom: 40px;
  text-align: center;
}

/* Connection Steps */
.connection-steps {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 40px;
  width: 100%;
  max-width: 700px;
}

.connection-steps .step {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  width: 120px;
}

.connection-steps .step-number {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #e0e0e0;
  color: #555;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 10px;
  transition: all 0.3s ease;
}

.connection-steps .step.completed .step-number {
  background-color: #4caf50;
  color: white;
}

.connection-steps .step.failed .step-number {
  background-color: #f44336;
  color: white;
}

.connection-steps .step-label {
  font-size: 14px;
  color: #666;
  text-align: center;
}

.connection-steps .step-connector {
  height: 3px;
  background-color: #e0e0e0;
  flex-grow: 1;
  margin: 0 10px;
  position: relative;
  top: -15px;
}

/* Connection Status */
.connection-status {
  text-align: center;
  background-color: white;
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  margin-bottom: 40px;
  width: 100%;
  max-width: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all 0.3s ease;
}

.connection-status.success {
  border-left: 6px solid #4caf50;
}

.connection-status.failed {
  border-left: 6px solid #f44336;
}

.connection-status.connecting {
  border-left: 6px solid #2196f3;
}

.connection-status h2 {
  font-size: 24px;
  font-weight: 600;
  margin-bottom: 16px;
  color: #333;
}

.connection-status p {
  font-size: 16px;
  color: #666;
  line-height: 1.6;
  margin-bottom: 0;
}

/* Loading Spinner */
.loading-spinner {
  width: 50px;
  height: 50px;
  border: 5px solid rgba(0, 0, 0, 0.1);
  border-top-color: #2196f3;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 20px;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

/* Success and Failed Icons */
.success-icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #4caf50;
  color: white;
  font-size: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.failed-icon {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: #f44336;
  color: white;
  font-size: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

/* Action Buttons */
.connect-device-actions {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.btn {
  padding: 12px 24px;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
  border: none;
}

.btn-primary {
  background-color: #4361ee;
  color: white;
}

.btn-primary:hover {
  background-color: #3a56d4;
}

.btn-outline {
  background-color: transparent;
  border: 2px solid #4361ee;
  color: #4361ee;
}

.btn-outline:hover {
  background-color: #f0f4ff;
}

/* Footer */
.connect-device-footer {
  padding: 16px;
  text-align: center;
  font-size: 14px;
  color: #777;
  background-color: #f1f1f1;
  margin-top: auto;
}

/* Responsive */
@media (max-width: 768px) {
  .connection-steps {
    flex-direction: column;
    margin-bottom: 30px;
  }
  
  .connection-steps .step {
    width: 100%;
    margin-bottom: 20px;
  }
  
  .connection-steps .step-connector {
    width: 3px;
    height: 20px;
    margin: 10px 0;
  }
  
  .connection-status {
    padding: 30px 20px;
  }
}

.connect-device-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  padding: 2rem;
}

.connect-device-content {
  max-width: 800px;
  width: 100%;
  background: white;
  padding: 3rem;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.connect-device-content h1 {
  text-align: center;
  color: #2c3e50;
  margin-bottom: 1rem;
}

.description {
  text-align: center;
  color: #7f8c8d;
  margin-bottom: 2rem;
}

.device-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.device-card {
  background: #f8f9fa;
  border-radius: 10px;
  padding: 1.5rem;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 2px solid transparent;
}

.device-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.device-card.selected {
  border-color: #3498db;
  background: #e3f2fd;
}

.device-icon {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.device-card h3 {
  color: #2c3e50;
  margin-bottom: 0.5rem;
}

.device-card p {
  color: #7f8c8d;
  font-size: 0.9rem;
}

.navigation-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
}

.back-button,
.continue-button {
  padding: 0.8rem 1.5rem;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.back-button {
  background-color: #e74c3c;
  color: white;
}

.continue-button {
  background-color: #2ecc71;
  color: white;
}

.back-button:hover,
.continue-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.continue-button:disabled {
  background-color: #95a5a6;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

/* Efficiency table styling within chart container */
.chart-container .efficiency-table {
  width: 100%;
  background: white;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  margin-top: 20px;
  overflow-x: auto;
}

.chart-container .efficiency-table table {
  width: 100%;
  border-collapse: collapse;
}

.chart-container .efficiency-table th,
.chart-container .efficiency-table td {
  padding: 12px 15px;
  text-align: left;
  border-bottom: 1px solid #dee2e6;
}

.chart-container .efficiency-table th {
  background-color: #f8f9fa;
  font-weight: 600;
  color: #343a40;
}

.chart-container .efficiency-table tr:hover {
  background-color: #f8f9fa;
}

/* Status indicators */
.efficiency-table .status-cell {
  font-weight: 500;
  padding: 4px 8px;
  border-radius: 4px;
  display: inline-block;
}

.efficiency-table .optimal {
  background-color: #e8f5e9;
  color: #2e7d32;
}

.efficiency-table .moderate {
  background-color: #fff8e1;
  color: #ff8f00;
}

.efficiency-table .low {
  background-color: #ffebee;
  color: #c62828;
}